
.content .top_cont img {
    width: 100%;
}

.banner_image img {
    width: 100%;
}

.body_post {
    padding: 20px;
}
.file_photo{
    display: none;
}
.photo_wrap{
    position: relative;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    overflow: hidden;

}
.photo_wrap img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.photo_wrap:hover .label_photo{
    opacity: 1;
}
.label_photo{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.7);
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;

}
.loading_box{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 100;
    background-color: #000000;
}